import React, { Component, Suspense } from 'react';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import EditWebEmbed from '../../EditWebEmbed'
import { getAssetsCount } from '../../../../../Services/Actions/assetsActions'

class EmbedList extends Component {
    state = {
        active: null
    }
    onClickToEdit = (item) => event => {
        var self = this
        getAssetsCount(item.id).then((response) => {
            const { publishCode, assestCount } = response.data
            self.props.onUpdateReponseData({ assestCount: assestCount, publishCode: publishCode })
        }).catch((ex) => {
            self.props.onUpdateReponseData({ assestCount: 0, publishCode: item })
        })


    }

    render() {
        const { state, history } = this.props
        const { active } = this.state
        return <Accordion defaultActiveKey="filter_list" className="filter_list">
            {state.collectionList.length > 0 && state.publishList.length > 0 && state.publishList.map((item, pIndex) => {
                return <Accordion.Item eventKey={pIndex}>
                    <div className="menu-item" key={pIndex}>
                        <Accordion.Header className="w-100 disable_arrow">
                            <div className="d-flex justify-content-between align-items-center w-100" onClick={this.onClickToEdit(item)}>
                                <span className="menu-title fw-bold text-ellipsis d-block me-3 fs-8">{item.name}</span>
                                <div className="d-flex align-items-center justify-content-end">
                                    <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Edit</Tooltip>}>
                                        <button type="button" className="btn btn-icon btn-secondary h-28px w-28px me-2">
                                            <span className="svg-icon svg-icon-6 me-0">
                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                    <path fill="none" d="M0 0h24v24H0z" />
                                                    <path d="M16.757 3l-2 2H5v14h14V9.243l2-2V20a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V4a1 1 0 0 1 1-1h12.757zm3.728-.9L21.9 3.516l-9.192 9.192-1.412.003-.002-1.417L20.485 2.1z" fill="currentColor" />
                                                </svg>
                                            </span>
                                        </button>
                                    </OverlayTrigger>
                                    <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Remove</Tooltip>}>
                                        <button type="button" className="btn btn-icon btn-secondary h-28px w-28px"
                                            onClick={this.props.onClickToDelete({ "id": item.id })}>
                                            <span className="svg-icon svg-icon-6 me-0">
                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                    <path fill="none" d="M0 0h24v24H0z" />
                                                    <path d="M7 4V2h10v2h5v2h-2v15a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V6H2V4h5zM6 6v14h12V6H6zm3 3h2v8H9V9zm4 0h2v8h-2V9z" fill="currentColor" />
                                                </svg>
                                            </span>
                                        </button>
                                    </OverlayTrigger>
                                </div>
                            </div>
                        </Accordion.Header>
                    </div>
                    <Accordion.Body className="p-0 pt-3">
                        <EditWebEmbed history={history} item={item} {...this.props} />
                    </Accordion.Body>
                </Accordion.Item>

            })
            }
        </Accordion>
    }

}
export default EmbedList

