import React, { Component } from 'react';
import moment from 'moment';
import { connect } from "react-redux";
//import Select from 'react-select';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { getPublishList } from '../../../../Services/Actions/publishActions';
import Filters from './Filters';
import Templates from './Template';
import EmbedList from './EmbedList'

const selectedPlatform = (platform) => {
    switch (platform) {
        case 'p-html':
            return 'HTML'
        case 'p-wordpress':
            return 'Wordpress'
        case 'p-wix':
            return 'Wix'
        case 'p-squarespace':
            return 'Squarespace'
        case 'p-shopify':
            return 'Shopify'
        case 'p-other':
            return 'Other'
        default:
            return `HTML`
    }
}

